.ribbon{
    position: absolute;
    z-index: 1;
    overflow: hidden;
    width: 75px;
    height: 75px;
    text-align: right;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    left: 10px;
    top: 11px;
}

.ribbon span{
    text-transform: uppercase;
    position:absolute;
    display: block;
    background-color: #ef582e;
    box-shadow:0 5px 10px rgba(0,0,0,0.1);
    color:#fff;
    text-shadow:0 1px 1px rgba(0,0,0,0.2);
    text-align:center;
    z-index: 1;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top:19px;
    left:-22px;
    width:104px;
    font-size: 10px;
    font-weight:bold;
    line-height:20px;
}

.ribbon::before, .ribbon::after{
    position: absolute;
    z-index: -1;
    content: close-quote;
    display: block;
    border: 5px solid #ef582e;
}

.ribbon::before{
    top:-3px;
    right:2px;
}

.ribbon::after{
    bottom:2px;
    left:-2px;
}
